import React from 'react';
import { Button, ButtonProps, Icons, ToastEmitter } from '@miq/componentjs';

// https://www.bennadel.com/blog/3961-having-fun-with-the-horizontal-usage-of-position-sticky-in-angular-11-0-5.htm

import './component.scss';

import { StaffCategory } from './Category';
import { StaffProduct } from './Product';
import { PriceDataType, ProductType } from './types';
import { getCN } from '@miq/utiljs';

type InstanceType = ReturnType<typeof StaffProduct> | ReturnType<typeof StaffCategory>;

interface UpdateButtonProps extends ButtonProps {
  instance: InstanceType;
  onSuccess: (r: any) => void;
}

export const PublishButton: React.FC<UpdateButtonProps> = (props) => {
  const { instance, onClick, onSuccess, onError, ...rest } = props;

  const handleClick = () => {
    if (instance.is_published) return null;
    return instance
      .publish()
      .then((res: any) => onSuccess?.(res))
      .catch((err: any) => onError?.(err));
  };

  return <Button onClick={handleClick} className="btn-primary btn-md" {...rest} disabled={instance.is_published} />;
};

export const UnPublishButton: React.FC<UpdateButtonProps> = (props) => {
  const { instance, onClick, onSuccess, onError, ...rest } = props;

  const handleClick = () => {
    if (!instance.is_published) return null;
    return instance
      .unpublish()
      .then((res: any) => onSuccess?.(res))
      .catch((err: any) => onError?.(err));
  };

  return <Button className="btn-danger btn-md" onClick={handleClick} {...rest} disabled={!instance.is_published} />;
};

//#region ================================== COMPONENTS =============================================================

export type PriceDisplayProps = PriceDataType & {
  className?: string;
  sale?: string | boolean;
  lineThrough?: string | boolean;
};

export const PriceDisplay = ({ amountWithSymbol, className, sale, lineThrough }: PriceDisplayProps) => (
  <div className={getCN(['miq-price-display', sale && 'text-danger', lineThrough && 'text-line-through', className])}>
    {amountWithSymbol}
  </div>
);

export const ProductPriceDisplay = ({
  retail_price_data,
  is_on_sale,
  sale_price_data,
  ...props
}: ProductType & { className?: string; style?: any }) => {
  if (!retail_price_data) return null;

  const isSale = is_on_sale && sale_price_data?.amountWithSymbol != null;

  return (
    <div className={getCN(['miq-product-price-display', props.className])} style={props.style}>
      {isSale && <PriceDisplay sale {...sale_price_data} className="miq-sale-price me-1" />}
      <PriceDisplay {...retail_price_data} lineThrough={isSale} className="miq-retail-price" />
    </div>
  );
};

