import { useSearchParams } from 'react-router-dom';

import { useRequest } from '@miq/hookjs';
import { StaffProduct } from './models';

export const useProductListRequest = (orderSlug?: string) => {
  const [params] = useSearchParams();

  const q = params.get('q');
  const cat = params.get('cat');
  const page = params.get('page');
  const published = params.get('published');
  const presale = params.get('presale');
  const sale = params.get('sale');

  let searchParams: any = { page, cat, published, presale, sale, q };
  if (orderSlug) searchParams.supplier_order_slug = orderSlug;

  return useRequest(() => StaffProduct().list({ params: searchParams }), {
    refreshDeps: [q, page, cat, published, presale, sale, orderSlug],
  });
};
