import * as React from 'react';
import { Routes, Route, useSearchParams } from 'react-router-dom';

import Staff, { StaffImg } from '@miq/staffjs';
import { Response } from '@miq/utiljs';
import { Table, Pagination, Viewports } from '@miq/componentjs';

import './indexview.scss';

import { StaffProduct } from './models';
import { PriceDisplay, ProductPriceDisplay, PublishedIcon } from '../components';
import { ProductType } from '../types';
import { ProductCardList } from './components';
import { useCrudEmitter } from '../events';
import { ProductListFilterForm } from './forms';
import { useProductListRequest } from './utils';

const ProductCreateView = React.lazy(() => import('./CreateView'));
const ProductStaffUpdateView = React.lazy(() => import('./UpdateView'));

export default function ProductStaffIndexView() {
  const { res, ...rest } = useProductListRequest();
  useCrudEmitter({ onUpdate: rest.updateItem, onDestroy: rest.removeItem });

  const r = new Response(res);

  if (!r.isSuccess) return null;

  return (
    <Routes>
      <Route path=":prodSlug/" element={<UpdateView {...{ response: r }} />} />
      <Route path="new" element={<ProductCreateView />} />
      <Route
        index
        element={
          <Staff.View
            title="Products"
            back="/staff/store/"
            actions={
              <Staff.Link to="new/" requiredPerms={['store.add_product']} className="btn btn-primary-3">
                Add product
              </Staff.Link>
            }
            requiredPerms={['store.view_product']}
          >
            <ProductListFilterForm categories={r.data?.categories} />
            <ProductStaffTable response={r} />
          </Staff.View>
        }
      />
    </Routes>
  );
}

const UpdateView = ({ response }: { response: Response }) => {
  return (
    <div className="d-grid grid-xl-4 gap-1">
      <Viewports.XLUp>
        <ProductCardList items={response.items} itempath="../" />
      </Viewports.XLUp>

      <div className="span-xl-3">
        <ProductStaffUpdateView back="/staff/store/products/" />
      </div>
    </div>
  );
};

const ProductStaffTable: React.FC<{ response: Response }> = (props) => {
  const { response } = props;
  const [params] = useSearchParams();

  if (response.items.length === 0)
    return (
      <div className="text-center p-staff-list-empty py-3 my-3">
        <p className="fw-light text-md">Add products to your shop to get started</p>
        <div className="my-4">
          <Staff.Link to="new/" requiredPerms={['store.add_product']} className="btn btn-primary btn-md">
            Add a new product
          </Staff.Link>
        </div>
      </div>
    );

  return (
    <div className="">
      <Table
        items={response.items}
        header={
          <>
            <Table.Th>Product</Table.Th>
            <Table.Th />
            <Table.Th>Price</Table.Th>
            <Table.Th>Availability</Table.Th>
          </>
        }
        className="product-staff-table w-100"
        renderItem={(product: ProductType) => {
          if (!product?.slug) return null;

          const instance = StaffProduct(product);
          const to = `${instance.slug}/${`${params}` ? `?${params}` : ''}`;
          return (
            <Table.Tr key={product.slug}>
              <Table.Th>
                <StaffImg.Thumb {...product?.cover_data} className="rounded" />
              </Table.Th>

              <Table.Td>
                <Staff.Link to={to}>
                  <div className="text-ellipsis">
                    {product.name}
                    <PublishedIcon show={product.is_published || false} />
                  </div>
                  <div className="">{product.category_data?.name}</div>
                </Staff.Link>
              </Table.Td>

              <Table.Td>
                <PriceDisplay {...product.retail_price_data} lineThrough={product.is_on_sale} />
              </Table.Td>
              <Table.Td>Av</Table.Td>
            </Table.Tr>
          );
        }}
      />

      <Pagination {...response.data} component={Staff.Link} to />
    </div>
  );
};
